.vertical-landing-content {
  @include vertical-landing-page-section;
  @include anchor-link-space;
  position: relative;
  z-index: 1;
}

.vertical-landing-content__bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  pointer-events: none;
  background: no-repeat center/cover;
}

.vertical-landing-content--gray-bg {
  @include vertical-landing-gray-bg;
}

.vertical-landing-content__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-content__header {
  @include vertical-landing-section-header;
}

.vertical-landing-content__title {
  @include vertical-landing-section-title;
}

.vertical-landing-content__grid {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: spacer(2);

  @supports (display: grid) {
    display: grid;
    grid-gap: rem(20);
    grid-template-columns: 1fr;

    @include breakpoints(small) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoints(medium) {
      grid-template-columns: repeat(3, 1fr);
    }

    @include breakpoints(large) {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    margin-left: -10px;
    margin-right: -10px;

    .vertical-landing-post {
      margin: 0 10px 20px;
      width: calc(100% - 20px);

      @include breakpoints(small) {
        grid-template-columns: repeat(3, 1fr);
        width: calc(50% - 20px);
      }

      @include breakpoints(medium) {
        grid-template-columns: repeat(4, 1fr);
        width: calc(33.33% - 20px);
      }

      @include breakpoints(large) {
        width: calc(25% - 20px);
      }
    }
  }
}

.vertical-landing-content__cta {
  @include vertical-landing-cta;
}
