/**
 * Body and page-specific styles for the vertical landing page.
 */

// Applied to page body.
.page-template-page_template-vertical-landing {
  padding: 0; // Allow full-bleed modules.

  /**
   * Below are overrides for existing page styles from
   * other theme entries.
   */
  .site {
    max-width: 100%;
    position: relative;
    z-index: 1;
  }

  .site-content__primary,
  .archive-content--author {
    @include breakpoints(medium, large) {
      margin-bottom: 0;
    }
  }

  .site-header {
    @include vertical-landing-furniture-container;
    background: transparent;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
    width: 100%;

    @include breakpoints($to:large) {
      z-index: 6;
    }
  }

  .site-header__site-logo {
    // stylelint-disable-next-line selector-max-specificity
    svg path {
      fill: $white;
    }
  }

  .site-footer-container {
    background: $blue-bg;
  }

  // stylelint-disable-next-line selector-max-specificity
  .site-header__menu-wrapper a:hover {
    // opacity: 0.7;
  }

  .site-footer__social-media-menu {
    // stylelint-disable-next-line selector-max-specificity
    .menu a {
      background-color: $white;
    }
  }

  .site-footer__copyright {
    color: $white;
  }

  .site-footer__utilities-menu {

    li,
    a {
      color: $white;
    }
  }

  &.use-dark-footer {
    // stylelint-disable-next-line selector-max-specificity
    .site-footer__utilities-menu {
      // stylelint-disable-next-line selector-max-specificity
      li,
      a {
        color: $steel;
      }
    }

    // stylelint-disable-next-line selector-max-specificity
    .site-footer__copyright {
      color: $steel;
    }

    // stylelint-disable-next-line selector-max-specificity
    .site-footer__social-media-menu {
      // stylelint-disable-next-line selector-max-specificity
      .menu a {
        background-color: $steel;
      }
    }
  }

  .site-footer {
    @include vertical-landing-furniture-container;
    padding-bottom: 0;
    padding-top: 0;

    // To override container styles
    @include breakpoints(small) {
      padding-bottom: 3rem;
      padding-top: 3rem;
    }
  }

  .site-content {
    margin-bottom: 0;
    padding: 0;
  }

  .site-header__mobile-toggle {
    background-color: transparent;
  }

  .sub-menu {
    @include breakpoints(large) {
      background: rgba(white,0.9);
      box-shadow: 0 0 7px 0.6px rgba($black,0.31);

      &::before {
        border-bottom: 11px solid rgba(white,0.9);
      }
    }
  }
}

.page-template-page_template-vertical-landing--hide-tagline {

  .site-header__site-subtitle {
    display: none;
  }
}
