.vertical-landing-nav {
  width: 100%;
}

.vertical-landing-nav__title {
  @include screen-reader-text;
}

.vertical-landing-nav__list {
  display: none;

  @include breakpoints(medium) {
    @include vertical-landing-inner-container;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
}

.vertical-landing-nav__link {
  display: block;
  font-family: font-family(sans-serif);
  font-size: rem(16);
  font-weight: 700;
  padding: rem(18) rem(22);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;

  @include breakpoints($to: sm-large) {
    padding: rem(18) rem(12);
  }

  &:hover,
  &:focus {
    // color: $white;
    outline: none;
  }
}

.vertical-landing-nav__inner {
  @include breakpoints(768px) {
    flex-direction: row-reverse;
  }

  @include breakpoints(s-large) {
    display: flex;
    justify-content: space-between;
  }

  .vertical-landing-nav__list {
    @include breakpoints(s-large) {
      justify-content: flex-start;
      width: auto;
      margin-left: -12px;
    }
    
    @include breakpoints(sm-large) {
      margin-left: -22px;
    }
  }
}

.vertical-landing-nav__ntw {
  display: flex;
  align-items: center;
  gap: 13px;

  @include breakpoints($to: s-large) {
    justify-content: center;
  }
  
  // stylelint-disable-next-line selector-max-specificity
  a,
  .title:hover {
    color: white;
  }

  .title {
    padding-right: 8px;

    @include breakpoints($to: s-large) {
      padding: 11px 8px 11px 0;
    }
  }

  a {
    text-decoration: none;
    font-size: 18px;
    display: block;
    margin-top: -2px;
  }
}
