.vertical-landing-hero {
  align-content: center;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 0;
  overflow: hidden;
  padding: 155px 0 25px;
  position: relative;
  z-index: 0;

  &::after {
    content: '';
    width: 0;
    min-height: 268px;
  }

  .container {
    width: 100%;
  }
}

.vertical-landing-hero__background {
  bottom: 0;
  height: 100%;
  left: 0;
  object-fit: cover;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  z-index: -1;
}

.vertical-landing-hero__inner {
  max-width: 600px;

  &.vertical-landing-hero__align-centered {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    max-width: 750px;
  }
}

.vertical-landing-hero__img {
  margin-bottom: 14px;
}

.vertical-landing-hero__background--mobile {
  @include breakpoints(marketomobilebkpt) {
    display: none;
  }

  ~ .vertical-landing-hero__background--desktop {
    @include breakpoints(none, marketomobilebkpt) {
      display: none;
    }
  }
}

.vertical-landing-hero__subtitle {
  @include vertical-landing-inner-container;
  font-family: font-family(sans-serif);
  font-size: rem(17);
  text-align: center;

  @include breakpoints(large) {
    font-size: rem(24);
  }
}

.vertical-landing-hero__title {
  font: 700 rem(24)/1.2 font-family(sans-serif);

  @include breakpoints(large) {
    font-size: rem(38);
  }
}
