.vertical-landing-subscribe {
  @include vertical-landing-page-section;
  @include anchor-link-space;
}

.vertical-landing-subscribe__thanks-msg {
  color: $white;
  text-align: center;

  h2 {
    color: $white;
    font-size: rem(36);
    margin-bottom: rem(24);
    font-weight: 600;
    line-height: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    align-items: flex-start;
    padding-left: 65px;
    padding-top: 20px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 45px;
      height: 45px;
      background: url("data:image/svg+xml,%3Csvg width='46' height='46' viewBox='0 0 46 46' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35.6254 24.3987C36.4397 28.2706 35.9178 32.3041 34.145 35.8412C32.3722 39.3784 29.453 42.2104 25.8638 43.8753C22.2746 45.5401 18.2272 45.9396 14.3817 45.0085C10.5363 44.0774 7.11986 41.8707 4.68977 38.7484C2.25969 35.626 0.959377 31.7724 1.00097 27.816C1.04256 23.8597 2.42359 20.0342 4.91878 16.9637C7.41396 13.8931 10.876 11.7587 14.7402 10.9087C18.6043 10.0586 22.6425 10.5431 26.1959 12.283' stroke='%23ED8B00'/%3E%3Cpath d='M8.5 26L17.5 35L45.5 1' stroke='white'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / contain;
    }
  }

  p {
    padding-left: 40px;
  }
}

.vertical-landing-subscribe__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-subscribe__title {
  color: $white;
  font-size: rem(36);
  font-weight: 600;
  line-height: 1;
  margin-bottom: rem(20);
  text-align: center;

  .use-dark-footer & {
    color: $steel;
  }
}

.vertical-landing-subscribe__text {
  color: $white;
  font-size: rem(24);
  font-weight: 300;
  line-height: 1.3;
  margin-bottom: rem(34);
  text-align: center;

  .use-dark-footer & {
    color: $steel;
  }
}

.vertical-landing-subscribe__form {
  align-items: center;
  display: flex;
  justify-content: center;

  input {
    color: $steel;
    font-size: rem(18);
    padding: rem(15);
    width: rem(370);
  }

  button {
    appearance: none;
    border: none;
    background-color: $steel;

    &:hover,
    &:focus {
      cursor: pointer;
    }
  }
}

.vertical-landing-subscribe__submit {
  height: rem(55);
  width: rem(55);
}
