.vertical-landing-staff__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-staff {
  @include vertical-landing-page-section;
  @include anchor-link-space;
}

.vertical-landing-staff--gray-bg {
  @include vertical-landing-gray-bg;
}

.vertical-landing-staff__header {
  @include vertical-landing-section-header;
}

.vertical-landing-staff__title {
  @include vertical-landing-section-title;
}

.vertical-landing-staff__grid {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: spacer(2);

  @supports (display: grid) {
    display: grid;
    grid-gap: rem(20);
    grid-template-columns: repeat(2, 1fr);

    @include breakpoints(small) {
      grid-template-columns: repeat(3, 1fr);
    }

    @include breakpoints(medium) {
      grid-template-columns: repeat(4, 1fr);
    }

    @include breakpoints(large) {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    margin-left: -10px;
    margin-right: -10px;

    .vertical-landing-staff-member {
      padding: 0 10px 20px;
      width: 50%;

      @include breakpoints(small) {
        grid-template-columns: repeat(3, 1fr);
        width: 33.33%;
      }

      @include breakpoints(medium) {
        grid-template-columns: repeat(4, 1fr);
        width: 25%;
      }

      @include breakpoints(large) {
        width: 16.667%;
      }
    }
  }
}
