.vertical-landing-cta {
  @include vertical-landing-page-section;
  @include anchor-link-space;
}

.vertical-landing-cta__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-cta__title {
  color: $white;
  font-size: rem(36);
  font-weight: 600;
  line-height: 1;
  margin-bottom: rem(20);
  text-align: center;

  .use-dark-footer & {
    color: $steel;
  }
}

.vertical-landing-cta__text {
  color: $white;
  font-size: rem(24);
  font-weight: 300;
  line-height: 1.3;
  margin-bottom: rem(30);
  text-align: center;

  .use-dark-footer & {
    color: $steel;
  }
}

.vertical-landing-cta__button-wrapper {
  text-align: center;
}

.vertical-landing-cta__button {
  outline: none;
  padding: 15px 30px;
  color: $white;
  font: 1.2rem / 1 'proxima-nova', 'Helvetica', 'Arial', sans-serif;
  text-decoration: none;
  display: inline-block;
  font-weight: 600;
}

.vertical-landing-cta__title + .vertical-landing-cta__button-wrapper {
  margin-top: rem(30);
}
