
.vertical-landing-staff-member__link {
  display: flex;
  overflow: hidden;
  position: relative;

  &:hover {

    // stylelint-disable-next-line selector-max-specificity
    .vertical-landing-staff-member__pic {
      cursor: pointer;
      transform: scale(1.2);
    }
  }
}

.vertical-landing-staff-member__meta-wrap {
  background: $blue-bg;
  bottom: 0;
  left: 0;
  opacity: 0.9;
  margin-bottom: 0;
  padding: rem(5) rem(10);
  position: absolute;
  width: 100%;
  z-index: 4;
}

.vertical-landing-staff-member__name {
  color: $white;
  font-size: (20);
  font-weight: 600;
  line-height: 1.4;
}

.vertical-landing-staff-member__title {
  color: $white;
  display: block;
  font-size: rem(14);
  line-height: 1.4;
}

.vertical-landing-staff-member__pic {
  display: block;
  height: 100%;
  margin-bottom: 0;
  object-fit: cover;
  transition: all 0.2s;
  width: 100%;
  z-index: 0;
}

.vertical-landing-staff__cta {
  @include vertical-landing-cta;
}

.vertical-landing-staff-member__tw {
  display: flex;
  align-items: center;
  background: #55acee;
  border-radius: 10px;
  overflow: hidden;
  max-width: 20px;
  height: 20px;
  position: absolute;
  z-index: 10;
  right: 10px;
  top: 10px;
  justify-content: flex-start;
  padding: 0 4px;
  font-size: 12px;
  text-decoration: none;
  color: $white;
  transition: 0.2s;

  img {
    width: 12px;
    flex-shrink: 0;
  }

  span {
    display: block;
    margin-left: 5px;
  }
}

.vertical-landing-staff-member {
  position: relative;
}
// stylelint-disable-next-line selector-max-specificity
.vertical-landing-staff-member__tw:hover {
  max-width: 103px;
}
