.vertical-landing-spotlight {
  padding: rem(36) 0;
  position: relative;
  z-index: 1;

  @include breakpoints(large) {
      padding: rem(72) 0;
  }

  h2 {
    color: $bayside;
    //font-size: 2.25rem;
    font-weight: 700;
    //line-height: 1.3;
    margin-bottom: 25px;
    font-size: 27px;
    line-height: 1.22;

    @include breakpoints(s-large) {
      //margin-bottom: 58px;
    }
  }

  .bg {
    position: absolute;
    bottom: 0;
    right: 0;
    object-fit: contain;
    object-position: 100% calc(100% + 36px);
    z-index: -1;
    width: 100%;

    @include breakpoints(s-large) {
      width: 57%;
    }
  }
}

.vertical-landing-spotlight__container {
  @include vertical-landing-inner-container;
}

.media-spotlight {
  //line-height: 1.2;

  @include breakpoints(s-medium) {
    //font-size: 19px;
  }

  & + .media-spotlight {
    margin-top: 90px;
  }

  h3 {
    font-size: 27px;
    line-height: 1.22;
    margin-bottom: 9px;
  }

  .more {
    text-decoration: none;
  }

  .head {
    margin-bottom: 18px;

    img {
        max-width: 250px;
    }
  }

  .body {
    @include breakpoints(s-medium) {
      display: flex;
    }
  }

  .left {
    flex: 1;
    border-top: 1px solid #D0D0CE;
    padding-top: 25px;
  }

  .right {
    display: flex;
    flex-direction: column;

    @include breakpoints(s-medium) {
      width: 200px;
      padding-bottom: 29px;
      margin-left: 25px;
    }

    @include breakpoints(s-large) {
      width: 255px;
      padding-right: 40px;
      margin-left: 46px;
    }
  }

  .news {
    flex: 1;

    @extend %listreset;

    @include breakpoints($to: s-medium) {
      margin-top: 40px;
    }

    // stylelint-disable selector-max-specificity
    .more {
      //font-size: 14px;
    }
  }

  .news-title {
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 17px;
    text-transform: uppercase;
    letter-spacing: -0.02em;
    background-color: $bayside;
    color: $white;
    display: inline-block;
    vertical-align: top;
    padding: 5px 19px 3px;
  }

  h4 {
    font-size: 17px;
    line-height: 1.24;
    margin-bottom: 25px;
  }

  .more {
    display: inline-block;
    vertical-align: top;
    //font-size: 17px;
    //font-weight: 700;
    //line-height: 1.66;
    //color: $avocado;
    //text-decoration: underline;
  }

  .more-main {
    @include breakpoints($to: s-medium) {
      order: -1;
    }
  }

  p {
    margin-bottom: 2rem;
  }

  figcaption {
    padding-top: 10px;
    font-size: 16px;

    @include breakpoints(s-medium) {
      min-height: 30px;
    }
  }
}

.vertical-landing-intro-text {
    + .vertical-landing-spotlight {
        padding-top: 0;
        margin-top: -2.5rem;
    }
}

.vertical-landing-investment-thesis--gray-bg {
    + .vertical-landing-spotlight {
        @include vertical-landing-gray-bg;
    }
}
