$color-vertical-gray: #f2f2f2;

@mixin vertical-landing-inner-container {
  margin-left: 2vw;
  margin-right: 2vw;
  max-width: $site-max-width;
  width: 94vw;

  @include breakpoints(large) {
    margin-left: auto;
    margin-right: auto;
  }
}

@mixin vertical-landing-page-section {
  padding: rem(36) 0;

  @include breakpoints(large) {
    padding: rem(72) 0;
  }
}

@mixin vertical-landing-section-header {
  margin-bottom: spacer(2);
}

@mixin vertical-landing-section-title {
  color: $steel;
  font-size: rem(36);
  font-weight: 700;
  line-height: 1.3;
}

@mixin vertical-landing-gray-bg {
  background-color: $color-vertical-gray;
}

@mixin vertical-landing-furniture-container {
  margin: 0 auto;
  max-width: $site-max-width;
}

@mixin vertical-landing-cta {
  font-family: font(sans-serif);
  font-size: rem(16);
  color: $steel;

  a {
    color: color(gray-blue);
    font-weight: 700;
    text-decoration: none;

    &:hover {
      color: $poppy;
    }
  }
}

@mixin anchor-link-space {

  &::before {
    display: block;
    content: " ";
    margin-top: 20px;
    height: 20px;
    visibility: hidden;
    pointer-events: none;
  }
}
