.vertical-landing-investment-thesis {
  @include vertical-landing-page-section;
  @include anchor-link-space;
}

.vertical-landing-investment-thesis__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-investment-thesis--gray-bg {
  @include vertical-landing-gray-bg;
}

.vertical-landing-investment-thesis__title {
  @include vertical-landing-section-title;
  margin-bottom: spacer(2);
}

.vertical-landing-investment-thesis__body-copy {
  @import "../site/wp-content";
}

.vertical-landing-investment-thesis__button {
  font-size: rem(16);
  text-decoration: none;

  &:hover {
    cursor: pointer;
  }
}

.vertical-landing-investment-thesis__img {
  display: block;
  margin: rem(50) auto 0;
}

.vertical-landing-investment-thesis__rich-text-wrap {
  overflow: hidden;

  &[aria-expanded="true"] {
    max-height: 100%;
  }

  &[aria-expanded="false"] {
    max-height: rem(100);
  }
}

.vertical-landing-investment-thesis__intro {
  [aria-expanded="true"] & {
    display: none;
  }
}

.vertical-landing-investment-thesis__main {
  [aria-expanded="true"] & {
    display: block;
  }
}

.vertical-landing-investment-thesis__intro {
  [aria-expanded="false"] & {
    display: block;
  }
}

.vertical-landing-investment-thesis__main {
  [aria-expanded="false"] & {
    display: none;
  }
}

.vertical-landing-investment-thesis__video {
  display: block;
  position: relative;
  margin-bottom: 45px;

  img {
    width: 100%;
  }

  .play-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 40px;
    height: 40px;
    background: url('../../images/icon-play.png') no-repeat center/contain;
    
    @include breakpoints(768px) {
      width: 72px;
      height: 72px;
    }
  }
}

.vertical-landing-investment-thesis__cp {
  max-width: 906px;

  h3 {
    font-size: 26px;
    margin-bottom: 30px;
  }
}

.vertical-landing-investment-thesis__cp-media {
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;

  > img {
      width: 64px;
      object-fit: contain;
      margin-right: 23px;
  }

  > div {
      flex: 1;
  }
}
