/**
 * Vertical landing page partner module.
 */

.vertical-landing-nonprofit_grid {
  display: grid;
  gap: 26px 48px;
  grid-template-columns: repeat(1, 1fr);
  line-height: 1.2;
  
  @include breakpoints(small) {
    grid-template-columns: repeat(2, 1fr);
  }
  
  @include breakpoints(medium) {
    padding-top: 40px;
  }

  .item {
    padding: 30px 0;
    min-height: 130px;
    border-top: 1px solid $fog;
    
    // stylelint-disable-next-line selector-max-specificity
    &:nth-child(1) {
      border-top: none;
    }

    // stylelint-disable-next-line selector-max-specificity
    &:nth-child(2) {
      @include breakpoints(small) {
          border-top: none;
      }
    }
  }

  .item-inner {
    text-decoration: none;
    color: inherit;
    
    @include breakpoints(large) {
      display: flex;
      align-items: flex-start;
    }

    // stylelint-disable-next-line selector-max-specificity
    &:hover img {
      filter: none;

      // stylelint-disable-next-line selector-max-specificity
      &[src*="CryptoTutors"],
      &[src*="Crypto-Tutors"],
      &[src*="crypto-tutors"] {
        filter: opacity(1);
      }
    }
  }

  .img {
    width: 180px;
    padding-bottom: 20px;

    @include breakpoints(large) {
      padding: 0 30px 0 0;
    }

    img {
      filter: grayscale(100%);
      transition: filter 0.3s ease-in;

      // stylelint-disable-next-line selector-max-specificity
      &[src*="CryptoTutors"],
      &[src*="Crypto-Tutors"],
      &[src*="crypto-tutors"] {
        filter: opacity(0.4);
      }
    }
  }

  .desc {
    flex: 1;
  }
}
