
.vertical-landing-post {
  background-color: $white;
  display: flex;
  flex-flow: column nowrap;
  height: 100%;
  min-height: rem(390);
  overflow: hidden;
  padding: rem(20);
  position: relative;
  text-decoration: none;

  > * {
    z-index: 1;
  }
}

.vertical-landing-post--no-image {
  border: 1px solid color(gray-very-light);

  &:hover {
    background-color: color(gray-very-light);
  }
}

.vertical-landing-post__post-type {
  align-items: center;
  display: flex;
  height: rem(33);
  left: 0;
  order: 0;
  padding: 0 rem(20);
  position: absolute;
  top: 0;
  width: 100%;
}

.vertical-landing-post__meta-wrap {
  margin-top: rem(33);
  position: relative;
  text-align: left;
}

.vertical-landing-post__title {
  color: $steel;
  font-size: rem(26);
  font-weight: 500;
  line-height: 1.3;
  margin-bottom: rem(20);
}

.vertical-landing-post__authors {
  color: $steel;
  font-size: rem(18);
  line-height: 1.3;
  margin-bottom: 0;
}

.vertical-landing-post__link {
  bottom: 0;
  padding: 0 rem(20) rem(10) 0;
  position: absolute;
  right: 0;
  text-decoration: none;
}

.vertical-landing-post__post-type-header--text,
.vertical-landing-post__link {
  text-transform: uppercase;
  font-weight: 500;
}
