/**
 * Vertical landing page portfolio module.
 */

.vertical-landing-portfolio {
  @include anchor-link-space;
  @include vertical-landing-page-section;
}

.vertical-landing-portfolio--gray-bg {
  @include vertical-landing-gray-bg;
}

.vertical-landing-portfolio__container {
  @include vertical-landing-inner-container;
}

.vertical-landing-portfolio__header {
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 0;
  padding-bottom: spacer(2);
}

.vertical-landing-portfolio__filter-nav {
  display: flex;
  justify-content: flex-end;
}

.vertical-landing-portfolio__categories-list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  margin-bottom: 0;

  .portfolio-filters__toggle {
    border-bottom: 0;
  }
}

.vertical-landing-portfolio__categories-item {
  margin-right: spacer(1);

  &:last-of-type {
    margin-right: 0;
  }
}

.vertical-landing-portfolio__filter {
  background: 0 0;
  border: none;
  color: color(gray-light);
  cursor: pointer;
  font-family: font-family(sans-serif);
  font-size: 1rem;
  text-decoration: none;

  &[aria-selected=true],
  &:hover {
    color: $poppy;
    outline: 0;
  }
}

.vertical-landing-portfolio__title {
  @include vertical-landing-section-title;
  margin-bottom: spacer(1.5);
}

.vertical-landing-portfolio-clf__title {
  @include vertical-landing-section-title;
}

.vertical-landing-portfolio__grid {
  @supports (display: grid) {
    grid-gap: rem(60);

    @include breakpoints(small) {
      grid-template-columns: repeat(5, 1fr);
    }
  }

  .company {

    // stylelint-disable-next-line selector-max-specificity
    &[aria-hidden=true] {
      display: none;
    }
  }
}
